<template>

    <b-container fluid>
        <div class="row">
            <div class="col-md-6">
                <Table :data="rows" :header="header" @onEdit="edited" @onDelete="deleted" @onCreate="created" @onSelect="selected" :actionable="true" :key="renderKey" header_title="Customers"/>
            </div>
            <div class="col-md-6">
                <Table :data="netsUsedRows" :header="netsUsedHeader" :key="netsUsedRenderKey" :actionable="false" :createable="false" header_title="Customer Details" />
            </div>
        </div>
    </b-container>
    
</template>

<script>
import Table from './Table.vue'
import axios from 'axios'

export default {
    components: {
        Table
    },
    data(){
        return {
            rows: [],
            header: [{
                    key: 'id',
                    label: 'Customer Id',
                    editable: false,
                    createable: true
                },
                {
                    key: 'name',
                    label: 'Name',
                    editable: true,
                    createable: true
                },
                {
                    key: 'blockCount',
                    label: 'Block Count',
                    editable: false,
                    createable: false
                }],
            renderKey: 0,
            netsUsedRenderKey: -1,
            netsUsedRows: [],
            netsUsedHeader: [
                {
                    key: 'BlockName',
                    label: 'Block Name',
                    editable: false,
                    createable: false
                },
                {
                    key: 'Category',
                    label: 'Category',
                    editable: false,
                    createable: false
                },
                {
                    key: 'DayFrom',
                    label: 'Day From',
                    editable: false,
                    createable: false
                },
                {
                    key: 'DayTo',
                    label: 'Day To',
                    editable: false,
                    createable: false   
                }
            ]
        }
    },
    mounted() {
        axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
        axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customers')
        .then(response => {
            this.rows = response.data
        })
        .catch(error => {
            console.log(error)
        })
    },
    methods: {
        edited(item){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.put(`https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customer?id=${item.id}`, {
                CustomerId: item.id,
                Name: item.name
            })
            .then(() => {
                axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
                axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customers')
                .then(response => {
                    this.rows = response.data
                    this.renderKey++
                })
                .catch(error => {
                    console.log(error)
                })                
            })
            .catch(error => {
                console.log(error)
            })
        },
        deleted(item){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.delete(`https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customer?id=${item.id}`)
            .then(() => {
                axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
                axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customers')
                .then(response => {
                    this.rows = response.data
                    this.renderKey++
                })
                .catch(error => {
                    console.log(error)
                })                
            })
            .catch(error => {
                console.log(error)
            })            
        },
        created(item){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.post(`https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customer`, {
                CustomerId: item.id,
                Name: item.name
            })
            .then(() => {
                axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
                axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customers')
                .then(response => {
                    this.rows = response.data
                    this.renderKey++
                })
                .catch(error => {
                    console.log(error)
                })                
            })
            .catch(error => {
                console.log(error)
            })            
        },
        selected(item){
            if(item){
                this.netsUsedRows = item.netsUsed
                console.log(item.netsUsed);
                this.netsUsedRenderKey--
            }
        }
    }
}
</script>