import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'

Vue.config.productionTip = false

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import '@fortawesome/fontawesome-free/css/all.css'
import '@fortawesome/fontawesome-free/js/all.js'

Vue.use(VueRouter)
Vue.use(BootstrapVue)
Vue.use(IconsPlugin)


const routes = [
  { path: '/', component: require('./components/Customers.vue').default },
  { path: '/blocks', component: require('./components/Blocks.vue').default },
  { path: '/nettypes', component: require('./components/NetTypes.vue').default },
  { path: '/customernets', component: require('./components/CustomerNets.vue').default },
]

const router = new VueRouter({
  routes,
  mode: 'history',
})


new Vue({
  render: h => h(App),
  router,
}).$mount('#app')