<template>
    <b-container>
        <Table :data="rows" :header="header" :actionable="true" :createable="true" :header_title="'Table'" />
    </b-container>
</template>

<script>
import Table from './Table.vue'
import axios from 'axios'

export default {
    components: {
        Table
    },
    data() {
        return {
            rows: [],
            header: [{
                    key: 'Id',
                    label: 'Id',
                    editable: false,
                    createable: true,
                    visible: false
                },
                {
                    key: 'Name',
                    label: 'Name',
                    editable: true,
                    createable: true
                },
                {
                    key: 'customerName',
                    label: 'Customer Name',
                    editable: false,
                    createable: false
                }],
        }
    },
    mounted() {
        axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
        axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/blocks')
        .then(response => {
            let rows_data = response.data
            for(var i = 0; i < rows_data.length; i++) {
                this.rows.push({
                    Id: rows_data[i].Id,
                    Name: rows_data[i].Name,
                    customerName: rows_data[i].CustomerId.Name
                })
            }

        })
        .catch(error => {
            console.log(error)
        })
    }
}
</script>