<template>
<div class="container">
  <!-- Header -->
  <b-row class="mt-3">
    <div class="col-md-9 h3">
      <div class="text-left">
        {{ header_title }}
      </div>
    </div>
    <div class="col-md-3">
      <b-button v-if="createable" size="sm" variant="primary" @click="addRow">Add</b-button>
    </div>
  </b-row>
  
  <!-- if createbale -->
  
  <b-table ref="table" striped hover outlined :items="data" :fields="header" selectable select-mode="single" @row-selected="rowSelected" responsive>
    <template #cell(actions)="row">
      <b-button size="sm" @click="edit(row.item, row.index, $event.target)" class="mx-2">
        Edit
      </b-button>
      <b-button size="sm" @click="delete_btn(row.item, row.index, $event.target)" class="mx-2 btn-danger" >
        Delete
      </b-button>
    </template>
  </b-table>

  <b-modal centered :id="addModal.id" :title="addModal.title" @hide="resetaddModal" hide-footer>
    <div class="d-flex justify-content-center">
      <b-form @submit.prevent="submitCreate">
        <b-form-group v-for="item in addModal.content" :key="item.id">
          {{item.label}} <b-form-input v-model="createdRow[item.key]" class="mx-2" :disabled="!item.createable "></b-form-input>
        </b-form-group>
        <b-button class="mt-2 btn-success" type="submit" variant="primary">Submit</b-button>
      </b-form>
    </div>
  </b-modal>
  
  <b-modal centered :id="editModal.id" :title="editModal.title" @hide="reseteditModal" hide-footer>
    <div class="d-flex justify-content-center">
      <b-form @submit.prevent="submit">
        <!-- v-for every item in data -->
        <b-form-group v-for="item in editModal.content" :key="item.id">
          <!-- {{item}} -->
          {{item.label}} <b-form-input v-model="editedRow[item.key]" class="mx-2" :disabled="!item.editable"></b-form-input>
        </b-form-group>
        
        <b-button class="mt-2 btn-success" type="submit" variant="primary">Submit</b-button>
      </b-form>
    </div>
  </b-modal>
</div>
</template>

<script>

export default {
  name: 'Table',
  components: {
    
  },
  props: {
    data: Array,
    header: Array,
    actionable: {
      type: Boolean,
      default: true
    },
    createable: {
      type: Boolean,
      default: true
    },
    header_title: {
      type: String,
      default: 'Table'
    }
  },
  mounted() {
    if(this.actionable) {
      this.header.push({
            key: 'actions',
            label: 'Actions',
            sortable: false,
          })
    }
  },
  data() {
    return {
      editModal: {
        id: 'edit-modal_'+Math.random(),
        title: '',
        content: ''
      },
      addModal: {
        id: 'add-modal_'+Math.random(),
        title: '',
        content: ''
      },
      editedRow: {
        
      },
      createdRow: {
        
      },
    }
  },
  methods: {
    edit(item, index, button) {
        // for every key in field, add to modal,
        this.editModal.title = 'Edit item'
        this.editModal.content = []
        this.header.forEach(field => {
          if(field.key=="actions"){
            return
          }
          field['value'] = item[field.key]
          this.editedRow[field.key] = item[field.key]
          this.editModal.content.push(field)
          }
        )
        console.log(this.editModal.id)
        this.$root.$emit('bv::show::modal', this.editModal.id, button)
      },
    reseteditModal() {
      this.editModal.title = ''
      this.editModal.content = ''
    },
    resetaddModal() {
      this.addModal.title = ''
      this.addModal.content = ''
    },
    submit(){
      this.$root.$emit('bv::hide::modal', this.editModal.id)
      this.$emit('onEdit', this.editedRow)
    },
    delete_btn(item){
      this.$emit('onDelete', item)
    },
    addRow(){
      this.addModal.title = 'Add item'
      this.addModal.content = []
      this.header.forEach(field => {
        if(field.key=="actions" || field.createable==false){
          return
        }
        field['value'] = ''
        this.editedRow[field.key] = ''
        this.addModal.content.push(field)
        }
      )
      this.$root.$emit('bv::show::modal', this.addModal.id)
      console.log(this.addModal.id)
    },
    submitCreate(){
      this.$root.$emit('bv::hide::modal', this.addModal.id)
      this.$emit('onCreate', this.createdRow)
    },
    rowSelected(item){
      let row = item[0];
      this.$emit('onSelect', row)
    }
  },
}
</script>
